const Post = ({ data }) => {
  const { id, frontmatter, excerpt, timeToRead } = data
  const { title, date } = frontmatter

  useEffect(() => {
    eva.replace()
  }, [])

  return (
    <li className={styles.post}>
      <div>
        <Link className={styles.link} to={`/post/${id}`}>
          <h2 className={styles.linkText}>{title}</h2>
        </Link>
        <p className={styles.excerpt}>{excerpt}</p>
        <div className={styles.info}>
          <span>🕙 {moment(date).format('ll')}</span>
          <span>
            ☕️
            {timeToRead}
            min read
          </span>
        </div>
      </div>
    </li>
  )
}

export default Post
